// import wPicker from "@/components/w-picker/w-picker.vue";
import Toast from '../../wxcomponents/vant/dist/toast/toast';
import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
var app = getApp().globalData
export default {
	
	data() {
		return {
		  finish:false,
		  fileList:[], //图片本地地址列表
		  imgArr:[], //上传阿里后的列表地址
		  content: '',
		  name: '',
		  address: '',  //绑定地址
		  category_id: '',
		  money: '',
		  plist:[],
		  showList :false,
		  lable:'',
		  list1:[],
		  province:'',
		  city:''
		}
	},
	watch: {
	    finish () {
	      if (this.finish) {
			  for(var i = 0; i<this.plist.length;i++){
				  this.lable += ","+this.plist[i]
			  }
			  this.lable=this.lable.substr(1)
			  // console.log(lable.str(0))
			this.imgArr = JSON.stringify(this.imgArr)
	        uni.hideLoading();
	        this.$api.Opportunity_create_et({
	          token: uni.getStorageSync("token"),
	          user_id: uni.getStorageSync("userId"),
			  type:1,
			  label:this.lable,
	          title: this.name,
	          money: this.money,
	          province:this.province,
			  city:this.city,
	          content: this.content,
	          images: this.imgArr
	        }).then(res => {
			  Toast(res.data.msg)
	          // this.$toast(res.msg)
	          if (res.data.code === '200') {
				  app.pList = []
				  uni.switchTab({
				      url: '/pages/index/index'
				  });
	          }
	        })
	      }
	    }
	},
	mounted() {
	  	
	},
	onShow() {
		// console.log(app)
		this.plist = app.pList
	},
	onLoad() {
		// 获取所需资金
	  	this.getMoney()
	},
	methods: {
		tabpick(){
			uni.navigateTo({
				url:'../../pagesMy/TabSelect/TabSelect?status=0'
			})
		},
		// 选择金额
		pickershop(e){
			this.money =this.list1[e.detail.value] 
		},
		// 所需资金
		getMoney(){
		      this.$api.Opportunity_money({
		      }).then(res => {
		        this.list1 = res.data.data
		      })
		},
		// 选择城市
		pickercity(e){
			this.address = e.detail.value[0].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔/g, '') + ' ' +e.detail.value[1].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔|城区/g, '')
			
			this.province = e.detail.value[0].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔/g, '')
			this.city = e.detail.value[1].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔|城区/g, '')
		},
		// 发布
		send () {
			  var that = this		      
		      if (this.name && this.plist.length > 0 && this.address && this.money && this.content && this.fileList.length > 0 ) {
		        Dialog.confirm({
		          title: '发布提示',
		          message: '请保证信息的真实性，因此产生的一切法律纠纷均由用户自行承担。平台审核中发现问题会立即下架您所发布的内容，确认发布？'
		        }).then(() => {
		          uni.showLoading({
		              title: '加载中'
		          });
		          async function put () {
		            for (let i = 0; i < that.fileList.length; i++) { 
					  await  uni.uploadFile({
								  url: app.aliUrl,
								  filePath: that.fileList[i].url,
								  name: 'file',
								  formData: {
								    key:"wx/"+"${filename}",
								    signature: app.signature,
								    policy:app.policy,
								    OSSAccessKeyId:app.OSSAccessKeyId,
								    success_action_status:'201'
								  }
						 }).then(res=>{
							 // console.log(res)
							 that.imgArr.push({
								 url_image:res[1].data.split('<Location>')[1].split("</Location>")[0]
							 })
							 if(that.fileList.length === that.imgArr.length){
								 that.finish = true
							 }
							 
						 })
		               }
		          }
		           put()
		        }).catch(() => {
		
		        })
		      } else {
		        if (!this.name) {
		          Toast('请填写标题')
		          return
		        }
				if (this.plist.length<=0) {
				  Toast('请选择标签')
				  return
				}
			
		        if (!this.address) {
		          Toast('请选择城市')
		          return
		        }
		        if (!this.money) {
		          Toast('请选择金额')
		          return
		        }
				if (!this.content) {
				  Toast('请填写内容')
				  return
				}
		        if (this.fileList.length <= 0) {
		          Toast('图片不能为空')
		          return
		        }
		        
		      }
		    },
		// 删除图片
		del(e){
			this.fileList = this.fileList.filter((item,index)=> index!=e.detail.index)
		},
		// 图片上传后的预览
		afterRead(event) {
		      const { file } = event.detail;
		      // 当设置 mutiple 为 true 时, file 为数组格式，否则为对象格式
		      this.fileList.push({url:file[0].path})
			  setTimeout(function() {
				  uni.pageScrollTo({
				  	scrollTop:1000
				  })
			  }, 300);
			 
		}
	}
}
